"use client";

import Image from "next/image";
import { useEffect, useState, useRef, useLayoutEffect } from "react";
import DynamicHeadline from "@/components/Hero/DynamicHeadline";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/dist/ScrollTrigger";

if (typeof window !== "undefined") gsap.registerPlugin(ScrollTrigger);

const HEADLINES = [
  "Power Up Your Potential",
  "Scale Up Your Revenue",
  "Amp Up Your Growth",
];

export default function Hero() {
  const [index, setIndex] = useState(0);
  const heroRef = useRef(null);

  useEffect(() => {
    const t = setInterval(
      () => setIndex((p) => (p + 1) % HEADLINES.length),
      3200
    );
    return () => clearInterval(t);
  }, []);

  return (
    <header ref={heroRef} className="relative overflow-hidden">
      <div className="max-w-7xl mx-auto px-6 py-20 grid grid-cols-1 md:grid-cols-2 gap-10 items-center">

        {/* Left */}
        <section>
          <h1 className="text-4xl md:text-6xl font-extrabold leading-tight text-[#0F172A]">
            Growth <span className="text-[#10B981]">Supercharged</span>
          </h1>
          <p className="mt-3 text-lg text-gray-600 font-medium">Own The Edge</p>

          <div className="mt-6">
            <div className="text-2xl md:text-3xl font-semibold h-12 overflow-hidden">
              <DynamicHeadline text={HEADLINES[index]} />
            </div>
          </div>

          <p className="mt-6 max-w-xl text-gray-600">
            We partner with companies to design growth strategies, performance
            marketing, and operational scaling—measured by data and driven by
            discipline.
          </p>

          {/* Mini Form */}
          <form
            onSubmit={(e) => e.preventDefault()}
            className="mt-8 w-full max-w-md grid grid-cols-1 md:grid-cols-3 gap-2"
          >
            <input
              placeholder="Your name"
              className="col-span-2 py-3 px-4 rounded-lg border focus:ring-2 focus:ring-emerald-200"
            />
            <button className="py-3 px-4 rounded-lg bg-[#10B981] text-white font-semibold">
              Get in touch
            </button>
          </form>
        </section>

        {/* Right - Image */}
        <aside className="relative w-full h-80 md:h-96 rounded-lg overflow-hidden shadow-lg bg-gray-50">
          <Image
            src="/images/hero.jpg"
            alt="Hero Image"
            fill
            className="object-cover"
          />
        </aside>
      </div>

      {/* Background Orbs */}
      <div className="pointer-events-none absolute inset-0 -z-10">
        <div className="absolute left-0 top-24 w-72 h-72 rounded-full bg-emerald-200 blur-3xl opacity-30 -translate-x-12" />
        <div className="absolute right-0 top-36 w-96 h-96 rounded-full bg-emerald-500 blur-3xl opacity-10 translate-x-10" />
      </div>
    </header>
  );
}
